// each snapshot item contains imageData and dataUrl
const initSave = {
	snapshots: [],
	currentSnapshotId: -1
};

let snapshotId = 0;

const save = (state = initSave, action) => {
	switch (action.type) {
	case "ADD_SNAPSHOT":
		return {
			snapshots: [
				{
					id: snapshotId++,
					imageData: action.imageData,
					dataUrl: action.dataUrl
				},
				...state.snapshots
			],
			currentSnapshotId: snapshotId - 1
		};
	case "REMOVE_SNAPSHOT":
		return {
			snapshots: state.snapshots.slice(0, state.snapshots.length - 1)
		};
	case "CHANGE_CURRENT_SNAPSHOT_ID":
		return {
			...state,
			currentSnapshotId: action.id
		};
	default:
		return state;
	}
};

export default save;



