const initPen = JSON.parse(localStorage.getItem("penOptions")) || {
	strokeWidth: 10,
	lineType: "solid",
	strokeColor: "#000000",
	strokeColorHistory: []
};

const pen = (state = initPen, action) => {
	switch (action.type) {
	case "CHANGE_PEN_STROKE_WIDTH":
		return {
			...state,
			strokeWidth: action.width
		};
	case "CHANGE_PEN_STROKE_COLOR":
		return {
			...state,
			strokeColor: action.color
		};
	case "CHANGE_PEN_STROKE_COLOR_HISTORY":
		return {
			...state,
			strokeColorHistory: action.colorArray
		};
	case "CHANGE_PEN_LINE_TYPE":
		return {
			...state,
			lineType: action.lineType
		};
	default:
		return state;
	}
};

export default pen;



