const initStamp = JSON.parse(localStorage.getItem("stampOptions")) || {
	maxWidth: 200,
	images: [],
	currentImage: null
};

const stamp = (state = initStamp, dispatch) => {
	switch (dispatch.type) {
	case "CHANGE_MAX_WIDTH":
		return {
			...state,
			maxWidth: dispatch.width
		};
	case "CHANGE_IMAGES":
		return {
			...state,
			images: dispatch.images
		};
	case "CHANGE_CURRENT_IMAGE":
		return {
			...state,
			currentImage: dispatch.image
		};
	default:
		return state;
	}
};

export default stamp;



