import React, { useEffect } from "react";
import { connect } from "react-redux";
import cn from "classnames";

const Stamp = ({
	stamp,
	initStampImages,
	handleFileChange,
	handleFilePick,
	handleWidthChange
}) => {
	// get default stamp from server
	useEffect(() => {
		if (stamp.images.length === 0) {
			fetch("http://localhost:3001/stamp")
				.then(res => res.json())
				.then(json => {
					initStampImages(json.images);
				});
		}
	}, []);

	// save stampOPtions to localStorage
	useEffect(
		() => {
			localStorage.setItem(
				"stampOptions",
				JSON.stringify({
					...stamp,
					images: [],
					currentImage: null
				})
			);
		},
		[stamp]
	);

	return (
		<div className="stamp">
			<h1>Stamp</h1>
			<section>
				<label>
					Max Width: {stamp.maxWidth}
					<input
						type="range"
						min="1"
						max="800"
						value={stamp.maxWidth}
						onChange={e => handleWidthChange(e.target.value)}
						name="max-width"
					/>
				</label>
			</section>
			<section>
				<label>
					Upload an Image
					<input
						type="file"
						className="stamp-file-upload"
						accept="image/*"
						onChange={e => handleFileChange(e.target.files[0], stamp)}
					/>
				</label>
			</section>
			{stamp.images && (
				<div className="stamp-images">
					{stamp.images.map((image, index) => {
						return (
							<img
								className={cn(
									"stamp-image",
									!(image === stamp.currentImage) && "stamp-image-blur"
								)}
								src={image}
								alt="I love gakki"
								key={index}
								onClick={e => handleFilePick(image)}
							/>
						);
					})}
				</div>
			)}
		</div>
	);
};

const mapStateToStampProps = state => {
	return {
		stamp: state.stamp
	};
};

const mapDispatchToStampProps = dispatch => {
	return {
		initStampImages: images => {
			dispatch({
				type: "CHANGE_IMAGES",
				images
			});
			dispatch({
				type: "CHANGE_CURRENT_IMAGE",
				image: images[0]
			});
		},
		handleWidthChange: width => {
			dispatch({
				type: "CHANGE_MAX_WIDTH",
				width
			});
		},
		handleFileChange: (file, stamp) => {
			if (file) {
				const reader = new FileReader();
				reader.readAsDataURL(file);
				reader.onload = img => {
					const image = img.target.result;

					// prevent duplication
					if (stamp.images.includes(image)) {
						alert("image already uploaded!");
						return;
					}
					let images = [];
					// if (stamp.images.length === 10) {
					// 	images = [image, ...stamp.images.slice(0, 9)];
					// } else {
					images = [image, ...stamp.images];
					// }
					dispatch({
						type: "CHANGE_CURRENT_IMAGE",
						image
					});
					dispatch({
						type: "CHANGE_IMAGES",
						images
					});
				};
			}
		},
		handleFilePick: image => {
			dispatch({
				type: "CHANGE_CURRENT_IMAGE",
				image
			});
		}
	};
};

export default connect(
	mapStateToStampProps,
	mapDispatchToStampProps
)(Stamp);



