// return the style of line type
export const getLineType = (type, width) => {
	if (type === "dash") {
		return [width * 5, width];
	} else if (type === "dot") {
		return [width, width];
	} else {
		return [];
	}
};

// calcute the start position, width, and height when drawing pictures
export const getImageDrawInfo = (width, height, posX, posY, maxWidth) => {
	const maxHeight = maxWidth * (height / width);
	const x = posX - maxWidth / 2;
	const y = posY - maxHeight / 2;
	return [x, y, maxWidth, maxHeight];
};

// remove repeated colors
export const removeRepeatedColor = (colors, color) => {
	let newColors = [...colors];
	if (colors.includes(color)) {
		newColors.splice(newColors.indexOf(color), 1);
	}
	return newColors;
};



