const initEraser = JSON.parse(localStorage.getItem("eraserOptions")) || {
	strokeWidth: 10,
	strokeColor: "#fff"
};

const eraser = (state = initEraser, action) => {
	switch (action.type) {
	case "CHANGE_ERASER_STROKE_WIDTH":
		return {
			...state,
			strokeWidth: action.width
		};
	default:
		return state;
	}
};

export default eraser;



