import React from "react";

import PenIcon from "./icons/pen-icon";
import EraserIcon from "./icons/eraser-icon";
import SaveIcon from "./icons/save-icon";
import StampIcon from "./icons/stamp-icon";
import cn from "classnames";

export default function NavBar({ activeTool, setActiveTool }) {
	return (
		<div className="nav-bar">
			<div
				className={cn(
					"nav-bar__tool",
					activeTool === "pen" && "nav-bar__tool--active"
				)}
				onClick={e => setActiveTool("pen")}
				title="Pen"
			>
				<PenIcon />
			</div>
			<div
				className={cn(
					"nav-bar__tool ",
					activeTool === "eraser" && "nav-bar__tool--active"
				)}
				onClick={e => setActiveTool("eraser")}
				title="Eraser"
			>
				<EraserIcon />
			</div>
			<div
				className={cn(
					"nav-bar__tool ",
					activeTool === "stamp" && "nav-bar__tool--active"
				)}
				onClick={e => setActiveTool("stamp")}
				title="Stamp"
			>
				<StampIcon />
			</div>
			<div
				className={cn(
					"nav-bar__tool ",
					activeTool === "save" && "nav-bar__tool--active"
				)}
				onClick={e => setActiveTool("save")}
				title="Save"
			>
				<SaveIcon />
			</div>
		</div>
	);
}



