import React from "react";
import { connect } from "react-redux";
import cn from "classnames";

const Save = ({ save, ctx, handleSnapshotClick }) => {
	const handleSave = el => {
		el.href = ctx.canvas.toDataURL();
	};
	return (
		<div className="save">
			<h1>Download</h1>
			<section>
				<a
					className="button download"
					download="myCanvas.png"
					href=""
					onClick={e => handleSave(e.target)}
				>
					Download Image
				</a>
			</section>
			<section>
				<label>Snapshots</label>
			</section>
			<div className="save-snapshots">
				{save.snapshots.map(snapshot => (
					<img
						className={cn(
							snapshot.id === save.currentSnapshotId && "current-selected-box"
						)}
						key={snapshot.id}
						alt="snapshot"
						src={snapshot.dataUrl}
						onClick={() =>
							handleSnapshotClick(snapshot.imageData, snapshot.id, ctx)
						}
					/>
				))}
			</div>
		</div>
	);
};

const mapStateToSaveProps = state => {
	return {
		save: state.save
	};
};

const mapDispatchToSaveProps = dispatch => {
	return {
		handleSnapshotClick: (imageData, id, ctx) => {
			dispatch({
				type: "CHANGE_CURRENT_SNAPSHOT_ID",
				id
			});
			ctx.putImageData(imageData, 0, 0);
		}
	};
};

export default connect(
	mapStateToSaveProps,
	mapDispatchToSaveProps
)(Save);



