import React, { useRef, useState, useEffect } from "react";
import { connect } from "react-redux";
import { getLineType, getImageDrawInfo } from "../util/util";

const Canvas = ({
	activeTool,
	pen,
	eraser,
	stamp,
	save,
	ctx,
	setCtx,
	handleAddSnapshot
}) => {
	const canvasRef = useRef(null);
	const [isDrawing, setIsDrawing] = useState(false);
	const [isErasing, setIsErasing] = useState(false);

	// componentDidMount
	useEffect(() => {
		const canvas = canvasRef.current;
		setCtx(canvas.getContext("2d"));

		// set canvas background
		const c = canvas.getContext("2d");
		c.fillStyle = "#fff";
		c.fillRect(0, 0, canvas.width, canvas.height);
		handleAddSnapshot(c, save.snapshots);
	}, []);

	const getX = event => {
		if (event.pageX === undefined) {
			return event.targetTouches[0].pageX - canvasRef.current.offsetLeft;
		} else {
			return event.pageX - canvasRef.current.offsetLeft;
		}
	};

	const getY = event => {
		if (event.pageY === undefined) {
			return event.targetTouches[0].pageY - canvasRef.current.offsetTop;
		} else {
			return event.pageY - canvasRef.current.offsetTop;
		}
	};

	const start = event => {
		// prevent the right mouse down
		if (event.button === 2) return;

		if (activeTool === "pen") {
			setIsDrawing(true);
			ctx.beginPath();
			ctx.moveTo(getX(event), getY(event));
		} else if (activeTool === "eraser") {
			setIsErasing(true);
			ctx.beginPath();
			ctx.moveTo(getX(event), getY(event));
		} else if (activeTool === "stamp") {
			const img = new Image();
			img.crossOrigin = "Anonymous";
			img.src = stamp.currentImage;

			ctx.drawImage(
				img,
				...getImageDrawInfo(
					img.width,
					img.height,
					getX(event),
					getY(event),
					stamp.maxWidth
				)
			);
			// add snapshot
			handleAddSnapshot(ctx, save.snapshots);
		}
		event.preventDefault();
	};

	const draw = event => {
		if (isDrawing) {
			ctx.lineTo(getX(event), getY(event));
			ctx.setLineDash(getLineType(pen.lineType, pen.strokeWidth));
			ctx.lineWidth = pen.strokeWidth;
			ctx.lineJoin = "round";
			ctx.strokeStyle = pen.strokeColor;
			ctx.stroke();
		} else if (isErasing) {
			ctx.lineTo(getX(event), getY(event));
			ctx.setLineDash([]);
			ctx.lineWidth = eraser.strokeWidth;
			ctx.lineJoin = "round";
			ctx.strokeStyle = eraser.strokeColor;
			ctx.stroke();
		}
		event.preventDefault();
	};

	const end = event => {
		if (isDrawing || isErasing) {
			ctx.stroke();
			ctx.closePath();
			setIsDrawing(false);
			setIsErasing(false);
			// add snapshot
			handleAddSnapshot(ctx, save.snapshots);
		}
		event.preventDefault();
	};

	return (
		<canvas
			id="canvas"
			width={window.innerWidth - 262}
			height={window.innerHeight}
			className="canvas"
			ref={canvasRef}
			onMouseDown={start}
			onMouseMove={draw}
			onMouseUp={end}
		/>
	);
};

const mapStateToCanvasProps = state => {
	return {
		pen: state.pen,
		eraser: state.eraser,
		stamp: state.stamp,
		save: state.save
	};
};

const mapDispatchToCanvasProps = dispatch => {
	return {
		handleAddSnapshot: (ctx, snapshots) => {
			const canvas = ctx.canvas;
			const imageData = ctx.getImageData(0, 0, canvas.width, canvas.height);
			const dataUrl = canvas.toDataURL();
			if (snapshots[0] && dataUrl === snapshots[0].dataUrl) return;
			// keep the length of snapshots to 4
			// if (snapshots.length === 4) {
			// 	dispatch({
			// 		type: "REMOVE_SNAPSHOT"
			// 	});
			// }
			dispatch({
				type: "ADD_SNAPSHOT",
				imageData,
				dataUrl
			});
		}
	};
};

export default connect(
	mapStateToCanvasProps,
	mapDispatchToCanvasProps
)(Canvas);



