import React, { useEffect } from "react";
import { connect } from "react-redux";

const Eraser = ({
	eraser,
	ctx,
	save,
	handleWidthChange,
	handleClearCanvas
}) => {
	// save eraserOptions to localStorage
	useEffect(
		() => {
			localStorage.setItem("eraserOptions", JSON.stringify(eraser));
		},
		[eraser]
	);

	return (
		<div>
			<h1>Eraser</h1>
			<section>
				<label>
					Stroke Width: {eraser.strokeWidth}
					<input
						type="range"
						min="1"
						max="100"
						value={eraser.strokeWidth}
						onChange={e => handleWidthChange(e.target.value)}
						name="stroke-width"
					/>
				</label>
			</section>
			<section>
				<button
					className="button"
					onClick={() =>
						handleClearCanvas(ctx, eraser.strokeColor, save.snapshots)
					}
				>
					Clear Canvas
				</button>
			</section>
		</div>
	);
};

const mapStateToEraserProps = state => {
	return {
		eraser: state.eraser,
		save: state.save
	};
};

const mapDispatchToEraserProps = dispatch => {
	return {
		handleWidthChange: width => {
			dispatch({
				type: "CHANGE_ERASER_STROKE_WIDTH",
				width
			});
		},
		handleClearCanvas: (ctx, color, snapshots) => {
			const canvas = ctx.canvas;
			ctx.fillStyle = color;
			ctx.fillRect(0, 0, canvas.width, canvas.height);

			// add snapshot
			const imageData = ctx.getImageData(0, 0, canvas.width, canvas.height);
			const dataUrl = canvas.toDataURL();
			if (!(dataUrl === snapshots[0].dataUrl)) {
				// keep the length of snapshots to 4
				// if (snapshots.length === 4) {
				// 	dispatch({
				// 		type: "REMOVE_SNAPSHOT"
				// 	});
				// }
				dispatch({
					type: "ADD_SNAPSHOT",
					imageData,
					dataUrl
				});
			}
		}
	};
};

export default connect(
	mapStateToEraserProps,
	mapDispatchToEraserProps
)(Eraser);



