import React from "react";

import Pen from "./panels/pen";
import Eraser from "./panels/eraser";
import Stamp from "./panels/stamp";
import Save from "./panels/save";

export default function ToolPanel({ activeTool, ctx }) {
	return (
		<div className="tool-panel">
			{activeTool === "pen" && <Pen />}
			{activeTool === "eraser" && <Eraser ctx={ctx} />}
			{activeTool === "stamp" && <Stamp />}
			{activeTool === "save" && <Save ctx={ctx} />}
		</div>
	);
}



