import React, { useEffect } from "react";
import cn from "classnames";
import { connect } from "react-redux";
import { removeRepeatedColor } from "../../util/util";

const Pen = ({
	pen,
	handleColorChange,
	selectColor,
	handleWidthChange,
	handleLineTypeChange
}) => {
	// save penOptions to localStorage when changed
	useEffect(
		() => {
			localStorage.setItem("penOptions", JSON.stringify(pen));
		},
		[pen]
	);
	return (
		<div className="panels-pen">
			<h1>Pen</h1>
			<section>
				<label>
					Stroke Color:
					<input
						type="color"
						value={pen.strokeColor}
						onChange={event =>
							handleColorChange(event.target.value, pen.strokeColorHistory)
						}
					/>
				</label>
				<div className="panels-pen-colors">
					{pen.strokeColorHistory.map((color, index) => (
						<div
							className={cn(
								color === pen.strokeColor && "current-selected-box"
							)}
							style={{ backgroundColor: color }}
							key={index}
							onClick={() => selectColor(color)}
						/>
					))}
				</div>
			</section>
			<section>
				<label>
					Stroke Width: {pen.strokeWidth}
					<input
						type="range"
						min="1"
						max="100"
						value={pen.strokeWidth}
						onChange={e => handleWidthChange(e.target.value)}
						name="stroke-width"
					/>
				</label>
			</section>
			<section>
				<label>Line Type:</label>
				<div className="line-types">
					<label
						className={
							pen.lineType === "solid" ? "type-selected" : "type-unselected"
						}
					>
						<input
							type="radio"
							value="solid"
							checked={pen.lineType === "solid"}
							onChange={e => handleLineTypeChange(e.target.value)}
						/>
						<span className="line-types__option">Solid</span>
					</label>
					<label
						className={
							pen.lineType === "dash" ? "type-selected" : "type-unselected"
						}
					>
						<input
							type="radio"
							value="dash"
							checked={pen.lineType === "dash"}
							onChange={e => handleLineTypeChange(e.target.value)}
						/>
						<span className="line-types__option">Dashed</span>
					</label>
					<label
						className={
							pen.lineType === "dot" ? "type-selected" : "type-unselected"
						}
					>
						<input
							type="radio"
							value="dot"
							checked={pen.lineType === "dot"}
							onChange={e => handleLineTypeChange(e.target.value)}
						/>
						<span className="line-types__option">Dotted</span>
					</label>
				</div>
			</section>
		</div>
	);
};

const mapStateToPenProps = state => {
	return {
		pen: state.pen
	};
};

const mapDispatchToPenProps = dispatch => {
	return {
		handleColorChange: (color, colors) => {
			let colorArray = removeRepeatedColor(colors, color);
			if (colorArray.length === 6) {
				colorArray = [color, ...colorArray.slice(0, 5)];
			} else {
				colorArray = [color, ...colorArray];
			}
			dispatch({
				type: "CHANGE_PEN_STROKE_COLOR",
				color
			});
			dispatch({
				type: "CHANGE_PEN_STROKE_COLOR_HISTORY",
				colorArray
			});
		},
		selectColor: color => {
			dispatch({
				type: "CHANGE_PEN_STROKE_COLOR",
				color
			});
		},
		handleWidthChange: width => {
			dispatch({
				type: "CHANGE_PEN_STROKE_WIDTH",
				width
			});
		},
		handleLineTypeChange: lineType => {
			dispatch({
				type: "CHANGE_PEN_LINE_TYPE",
				lineType
			});
		}
	};
};

export default connect(
	mapStateToPenProps,
	mapDispatchToPenProps
)(Pen);



